export default {
	data() {
		return {
			godetailIndex:'',//跳转参数
		}
	},
	onReady() {
		this.backHome();
	},
	methods: {
		//路由统一事件
		godetail: function(e) {
			if(e==0){ 
				this.godetailIndex=1;
				uni.switchTab({
					url: '../../order/OrderList'	
				});
			}else if(e==1){
				this.godetailIndex=2;
				uni.switchTab({
					url: '/pages/Home/indexZhly'
				});
			}
		},
		backHome(){
			setTimeout(() => {
				if(this.godetailIndex == ''){
					uni.switchTab({
						url: '/pages/Home/indexZhly'
					});
				}
			}, 10000)
			
		}

	}
}
